        ".sticky-element-wrap": stickyElement,
        ".practice-areas": anchorsArea,
        ".jcf-scrollable": customScroll,
        ".development-programs": developmentProgramSlider,
        ".clients-testimonials-slider": clientsTestimonialsSlider,
        ".news-alert-slider": newsAlertSlider,
        ".resources-slider": resourcesSlider,
        ".news-alert": newsAlert,
        ".images-slider": imagesSlider,
        ".print-ico": printArticle,
        ".hero-section": heroSectionVideo
