export function cookiesPopup() {
    let cookie = $('.cookies-popup');
    let closeCookie = cookie.find('.close, .clear');
    let acceptCookie = cookie.find('.accept-cookie');

    if (document.cookie.indexOf("cookieAccess=") >= 0) {
        cookie.hide();
    } else {
        cookie.addClass("show");
        $("body").addClass('cookie-showed');
    }

    closeCookie.on("click", function (e) {
        e.preventDefault();
        cookie.removeClass("show");
        $("body").removeClass('cookie-showed');
    });

    acceptCookie.on("click", function (e) {
        e.preventDefault();
        let expiry = new Date();
        expiry.setTime(expiry.getTime() + 360 * 24 * 60 * 60 * 1000);
        document.cookie = "cookieAccess=yes; path=/; expires=" + expiry.toGMTString();
        cookie.removeClass("show");
        $("body").removeClass('cookie-showed');
    });
}


