export function newsAlert() {
    let cookie = $('.news-alert');
    let closeCookie = cookie.find('.close, .clear');

    if (document.cookie.indexOf("newsAlert=") >= 0) {
        cookie.slideUp(400);
    } else {
        cookie.addClass("show");
    }

    closeCookie.on("click", function (e) {
        e.preventDefault();
        let expiry = new Date();
        expiry.setTime(expiry.getTime() + 24 * 60 * 60 * 1000);
        document.cookie = "newsAlert=yes; path=/; expires=" + expiry.toGMTString();
        cookie.slideUp(400);
    });
}


