export function limitedContent() {
    let $parent = $(".limited-information");
    let addMoreLink = $parent.find(".add-more")
    addMoreLink.on("click", function () {
        if (!addMoreLink.hasClass('active')) {
            addMoreLink.addClass("active").html('<span>Show Less</span>');
            $parent.find(".hide").slideDown(300);
        } else {
            addMoreLink.removeClass("active").html('<span>Show More</span>');
            $parent.find(".hide").slideUp(300);
        }
    });
}


