export function resourcesSlider() {
    $('.resources-slider').slick({
        infinite: true,
        speed: 300,
        slidesToShow: 1,
        slidesToScroll: 1,
        variableWidth: true,
        prevArrow: "<button type='button' class='slick-prev slick-arrow'><svg width=\"15\" height=\"28\" viewBox=\"0 0 15 28\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
            "<path d=\"M14 27L0.999999 14L14 1\" stroke=\"white\"/>\n" +
            "</svg></button>",
        nextArrow: "<button type='button' class='slick-next slick-arrow'><svg width=\"15\" height=\"28\" viewBox=\"0 0 15 28\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
            "<path d=\"M1 1L14 14L1 27\" stroke=\"white\"/>\n" +
            "</svg>\n</button>",
        mobileFirst: true,
        responsive: [
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 2,
                }
            },
            {
                breakpoint: 1023,
                settings: {
                    slidesToShow: 3
                }
            }
        ]
    });
}


