export function mobileNav() {
    const body = $("body");
    const $window = $(window);
    const btnMenu = $(".btn-menu");
    const arrowLink = `<span class="arrow"><svg width="12" height="8" viewBox="0 0 12 8" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M0.5 1.25L6 6.75L11.5 1.25" stroke="white" stroke-linecap="round" stroke-linejoin="round"/>
</svg>
</span>`;

    btnMenu.on("click", function (e) {
        let $this = $(this);
        if (!$this.hasClass("open")) {
            $this.addClass("open");
            body.addClass("menu-opened");
        } else {
            $this.removeClass("open");
            body.removeClass("menu-opened");
        }
        e.preventDefault();
    });

    $(arrowLink).insertBefore(".sub-menu");

    $(document).on("click", ".main-menu .arrow", function () {
        let $this = $(this);
        let parent = $this.parent();
        if ($window.width() < 1200) {
            if (!parent.hasClass("active")) {
                parent.addClass("active").find(".sub-menu").slideDown();
            } else {
                parent.removeClass("active").find(".sub-menu").slideUp();
            }
        }
        if ($window.width() > 1200 && body.hasClass('touch')) {
            if (!parent.hasClass("active")) {
                parent.addClass("active").find(".sub-menu").fadeIn();
            } else {
                parent.removeClass("active").find(".sub-menu").fadeOut();
            }
        }
    });

}


