export function videoIframe() {
    let videoHolder = $(".video-holder");
    videoHolder.on("click", ".play-button", function (e) {
        e.preventDefault();
        var parent = $(this).closest(".video-holder");
        var commandPlay = {
            "event": "command",
            "func": "playVideo"
        };
        var src = $(this).attr("href") + '?enablejsapi=1&autoplay=1';
        var video = '<iframe src="' + src + '" frameborder="0" allow="autoplay" allowfullscreen></iframe>';
        parent.append(video);

        if (!parent.hasClass("video-play")) {
            parent.find('iframe')[0].contentWindow.postMessage(JSON.stringify(commandPlay), "*");
            parent.addClass("video-play");
        }
    });
}


