export function accordion() {
    let $accordionItem = $(".accordion-item h5 > a");
    $accordionItem.on("click", function (e) {
        e.preventDefault();

        let $this = $(this);

        if ($this.closest('.accordion-item').find(".jcf-scrollable").length) {
            jcf.destroyAll();
        }
        if (!$this.hasClass('active')) {
            $accordionItem.removeClass('active').parent().next().slideUp(400);
        }
        $this.toggleClass("active").parent().next().slideToggle(400);
        if ($this.closest('.accordion-item').find(".jcf-scrollable").length) {
            setTimeout(() => {
                jcf.replaceAll();
            }, 100);
        }
    });
    $(".accordion").each(function () {
        if (!$('.accordion-item:first-child h5 > a').hasClass('active')) {
            $(this).find(".accordion-item:first-child h5 > a").trigger('click');
        }
    });
}


