export function filterActions() {
    let $parent = $(".filter-for-listing");
    $parent.on("click", ".add-more", function (e) {
        e.preventDefault();
        $(this).toggleClass("active").next().slideToggle(300);
    });
    $parent.on("click", ".clear", function () {
        if ($parent.find(".add-more").hasClass("active")) {
            $parent.find(".add-more").toggleClass("active").next().slideToggle(300);
        }
    });
}


