export function imagesSlider() {
    const slider = $('.images-slider');

    slider.slick({
        dots: true,
        infinite: true,
        speed: 300,
        slidesToShow: 1,
        appendArrows: ".images-slider-nav",
        appendDots: ".images-slider-nav",
        prevArrow: "<button type='button' class='slick-prev slick-arrow'><svg width=\"8\" height=\"13\" viewBox=\"0 0 8 13\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
        "<path d=\"M7 1L1 6.5L7 12\" stroke=\"#1C1C1C\" stroke-linecap=\"round\" stroke-linejoin=\"round\"/>\n" +
        "</svg>\n</button>",
        nextArrow: "<button type='button' class='slick-next slick-arrow'><svg width=\"8\" height=\"13\" viewBox=\"0 0 8 13\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
        "<path d=\"M1 12L7 6.5L1 1\" stroke=\"#1C1C1C\" stroke-linecap=\"round\" stroke-linejoin=\"round\"/>\n" +
        "</svg></button>"
    });

    $('.slick-btn').on('click', function () {
        if ($(this).hasClass('button-prev')) {
            slider.slick('slickPrev');
        } else {
            slider.slick('slickNext');
        }
    })
}


