export function anchorLink() {
    $(".anchor-link").on("click", function (e) {
        e.preventDefault();
        let $target = $(this).attr("href");
        $("html, body").animate({scrollTop: $($target).offset().top - $(".header").innerHeight()}, 1000);
    });
}


