export function newsAlertSlider() {
    let $slider = $(".news-alert-slider");
    let sliderNav = $(".news-alert-slider-nav");
    let sliderCurrentHolder = sliderNav.find(".current");
    let sliderAmountHolder = sliderNav.find(".amount");
    let sliderAmount = $slider.find(".item").length;
    if (sliderAmount > 1) {
        sliderCurrentHolder.text(1);
        sliderAmountHolder.text(sliderAmount);
        $(".nav-count").show(300);
    }
    $slider.slick({
        infinite: true,
        fade: true,
        speed: 300,
        slidesToShow: 1,
        adaptiveHeight: true,
        autoplay: true,
        autoplaySpeed: 4000,
        appendArrows: ".news-alert-slider-nav",
        prevArrow: "<button type='button' class='slick-prev slick-arrow'><svg width=\"8\" height=\"13\" viewBox=\"0 0 8 13\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
            "<path d=\"M7 1L1 6.5L7 12\" stroke=\"#7A7A7A\" stroke-linecap=\"round\" stroke-linejoin=\"round\"/>\n" +
            "</svg>\n</button>",
        nextArrow: "<button type='button' class='slick-next slick-arrow'><svg width=\"8\" height=\"13\" viewBox=\"0 0 8 13\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
            "<path d=\"M1 12L7 6.5L1 1\" stroke=\"#7A7A7A\" stroke-linecap=\"round\" stroke-linejoin=\"round\"/>\n" +
            "</svg></button>"
    }).on('beforeChange', function (event, slick, currentSlide, nextSlide) {
        sliderCurrentHolder.text(nextSlide + 1);
    });
}


