export function popup() {
    const $body = $("body");
    let $popupInfo = $(".popup-info");
    let $topicsList = $(".topics-list");
    $(".popup-opener").on("click", function (e) {
        e.preventDefault();
        let target = $(this).attr("href");
        $body.addClass("popup-open");
        $(target).show(10);
        if (target === '#bio-popup') {
            const email = $(this).text();
            $(target).find('.button').attr('href', `mailto:${email}`)
        }
    });
    $popupInfo.find(".close").on("click", function (e) {
        e.preventDefault();
        $body.removeClass("popup-open");
        setTimeout(function () {
            $popupInfo.hide(100);
        }, 800);
    });


    $popupInfo.find(".topics-heading .select-all").on("click", function () {
        let $this = $(this);
        if (!$this.hasClass("checked")) {
            $this.addClass("checked").text("Unselect All");
            $topicsList.find(":checkbox").each(function () {
                this.checked = true;
            });
        } else {
            $this.removeClass("checked").text("Select All");
            $topicsList.find(":checkbox").each(function () {
                this.checked = false;
            });
        }
    });
    $topicsList.find("input").on("change", function () {
        let check = $topicsList.find("input[type=checkbox]:checked").length;
        console.log(check < $topicsList.find(":checkbox").length);
        if (check < $topicsList.find(":checkbox").length) {
            $(".topics-heading .select-all").removeClass("checked").text("Select All").parent().find(":checkbox").prop('checked', false);
        }
    });
}


