export function anchorsArea() {
    $('.practice-areas-list .item').each((i, item) => {
        const title = $(item).find('h4').text().toLowerCase().split(/[ ,]+/)[0];
        $(item).attr('id', title);
    });
    $('.practice-areas-filter li a').each((i, item) => {
        const title = $(item).text().toLowerCase().split(/[ ,]+/)[0];
        $(item).attr('href', title);
        $(item).on('click', function (e) {
            e.preventDefault();
            let $target = '#' + $(this).attr("href");
            $("html, body").animate({scrollTop: $($target).offset().top - $(".header").innerHeight() - 30}, 1000);
        })
    });
}


