export function developmentProgramSlider() {
    const sliderNav = $(".development-programs-nav button");
    const slider = $(".development-programs-slider");
    slider.slick({
        slidesToScroll: 1,
        slidesToShow: 1,
        infinite: false,
        arrows: false,
        mobileFirst: true,
        dots: true,
        adaptiveHeight: true,
        draggable: false,
        responsive: [
            {
                breakpoint: 1023,
                settings: {
                    dots: false,
                    arrows: false,
                    fade: true
                }
            }
        ]
    });

    sliderNav.on("click", function () {
        const $this = $(this);
        const parent = $this.parent();
        const index = parent.index();
        slider.slick('slickGoTo', index);
        parent.siblings().removeClass("active");
        parent.addClass("active");
    });
}


